<template>
    <div class="setAddress" can-swipe-back="false">
        <!-- <van-nav-bar class="task-nav-bar" :title="flag==1||flag== 3?'编辑收货地址':'新增收货地址'" :border="false" left-arrow
            @click-left="goBack" /> -->
            <navigation-bar  @onLeftClick="goBack" :pageName="flag==1||flag== 3?'编辑收货地址':'新增收货地址'" />
        <div class="setAddressContent has-header" overflow-scroll="false">
            <div :class="themeCurrent == '1' ? 'ressInfo-l' : 'ressInfo'">
                <div class="consigneeInfo">
                    <label for="consignee">收货人：</label>
                    <input :class="this.$devicePlatform == 'ios'?'inputLineHeight':''" id="consignee" type="text" v-model="recName" maxlength="10">
                </div>
                <div class="consigneeInfo">
                    <label for="phoneNum">手机号码：</label>
                    <input :class="this.$devicePlatform == 'ios'?'inputLineHeight':''" id="phoneNum"  type="tel"  oninput="if(value.length > 11)value = value.slice(0, 11)" maxlength="11" v-model="recPhone" onkeyup="value=value.replace(/[^\d]/g,'')"/>
                </div>
                <div class="consigneeInfo reginInfo">
                    <div class="regionRight">
                        <label for="region">所在地区：</label>
                        <div class="regionTxt" @click="showSelectBox()" v-if="!regionData.recProvince&&!isVirtualArea">请选择省/市/区/小区
                        </div>
                        <div class="regionTxt" @click="showSelectBox()" v-if="!regionData.recProvince&&isVirtualArea">请选择省/市/区
                        </div>

                        <div class="reginLeftTxt" @click="showSelectBox()" v-if="regionData.recProvince">
                            {{regionData.recProvince}} {{regionData.recCity}} {{regionData.recDistrict}}
                            {{regionData.recAreaName}}</div>
                    </div>
                    <img src="@/assets/images/home/property_payment/icon_arrow.png" alt="" @click="showSelectBox()">

                </div>
                <div class="consigneeInfo">
                    <label for="site">详细地址：</label>
                    <input :class="this.$devicePlatform == 'ios'?'inputLineHeight':''" id="site" type="text" v-model="recAddressDetail" maxlength="50">
                </div>
            </div>
            <div class="lineBox"></div>
            <!-- 设置默认地址 -->
            <div class="defaultBox">
                <div :class="themeCurrent == '1' ? 'defaultBtn-l' : 'defaultBtn'">设置默认地址</div>
                <van-switch v-model="checked" active-color="#FB5768"  inactive-color="#D1D1D1" size="26px"/>
            </div>
            <div class="lineBox" style="height: 15px"></div>
            <!-- 删除地址 -->
            <div class="removeBox" v-if='flag == 1||flag == 3'>
                <div :class="themeCurrent == '1' ? 'removeBtn-l' : 'removeBtn'" @click="delRecAddrBtn()">删除地址</div>
            </div>
            <!-- 保存 -->
            <div :class="themeCurrent == '1' ? 'saveBtn-l' : 'saveBtn'" @click="saveData()">保存</div>
        </div>

        <!-- 配送地址选择 -->
        <div class="fixedLayer" v-if="isShowSelectBox" @click="hideSelectBox()"></div>
        <div class="setReginBox" v-if="isShowSelectBox">
            <div class="setReginHead">
                <div class="retreatBtn" @click="hideSelectBox()">
                    <img src="@/assets/images/common/left_black.png" alt="">
                </div>
                <div :class="themeCurrent == '1' ? 'title-l' : 'title'">配送至</div>
            </div>
            <div class="selectBox">
                <div :class="themeCurrent == '1' ? 'pitchUpTxt-l' : 'pitchUpTxt'" v-if="regionData.recProvince" @click="queryProvince()">
                    {{regionData.recProvince}}</div>
                <div :class="themeCurrent == '1' ? 'pitchUpTxt-l city' : 'pitchUpTxt city'" v-if="regionData.recCity" @click="getCityList(regionData.recProvince)">
                    {{regionData.recCity}}</div>
                <div :class="themeCurrent == '1' ? 'pitchUpTxt-l district' : 'pitchUpTxt district'" v-if="regionData.recDistrict"
                    @click="queryDistrict(regionData.recCity)">{{regionData.recDistrict}}</div>
                <div :class="themeCurrent == '1' ? 'pitchUpTxt-l district' : 'pitchUpTxt district'" v-if="regionData.recAreaName"
                    @click="getAreaList(regionData.recDistrict)">{{regionData.recAreaName}}</div>
                <div class="plaseSelect" v-if="!regionData.recAreaName">
                    <div :class="themeCurrent == '1' ? 'selectTxt-l' : 'selectTxt'">请选择</div>
                    <div class="signBox">
                        <span class="sign"></span>
                    </div>
                </div>
            </div>
            <div ref="selectContent" class="selectContent" scrollbar-y='false' delegate-handle="myScroll" has-bouncing='false' v-if="themeCurrent == '0'">
                <div class="selectItem" v-if="selectDataIndex==0" v-for="(selectItem,i) in selectData"
                    :class="{'pitchUpItem':selectIndex == i}" @click="selectCity(i)">
                    <span>{{selectItem.provinceName}}</span>
                    <img src="@/assets/images/mall/user_addressList_icon_choice.png" alt="">
                </div>
                <div class="selectItem" v-if="selectDataIndex==1" v-for="(selectItem,i) in selectData"
                    :class="{'pitchUpItem':selectIndex == i}" @click="selectCity(i)">
                    <span>{{selectItem.cityName}}</span>
                    <img src="@/assets/images/mall/user_addressList_icon_choice.png" alt="">
                </div>
                <div class="selectItem" v-if="selectDataIndex==2" v-for="(selectItem,i) in selectData"
                    :class="{'pitchUpItem':selectIndex == i}" @click="selectCity(i)">
                    <span>{{selectItem.districName}}</span>
                    <img src="@/assets/images/mall/user_addressList_icon_choice.png" alt="">
                </div>
                <div class="selectItem" v-if="selectDataIndex==3" v-for="(selectItem,i) in selectData"
                    :class="{'pitchUpItem':selectIndex == i}" @click="selectCity(i)">
                    <span>{{selectItem.areaName}}</span>
                    <img src="@/assets/images/mall/user_addressList_icon_choice.png" alt="">
                </div>
            </div>
            <div ref="selectContent" class="selectContent-l" scrollbar-y='false' delegate-handle="myScroll" has-bouncing='false' v-if="themeCurrent == '1'">
                <div class="selectItem-l" v-if="selectDataIndex==0" v-for="(selectItem,i) in selectData"
                    :class="{'pitchUpItem-l':selectIndex == i}" @click="selectCity(i)">
                    <span>{{selectItem.provinceName}}</span>
                    <img src="@/assets/images/mall/user_addressList_icon_choice.png" alt="">
                </div>
                <div class="selectItem-l" v-if="selectDataIndex==1" v-for="(selectItem,i) in selectData"
                    :class="{'pitchUpItem-l':selectIndex == i}" @click="selectCity(i)">
                    <span>{{selectItem.cityName}}</span>
                    <img src="@/assets/images/mall/user_addressList_icon_choice.png" alt="">
                </div>
                <div class="selectItem-l" v-if="selectDataIndex==2" v-for="(selectItem,i) in selectData"
                    :class="{'pitchUpItem-l':selectIndex == i}" @click="selectCity(i)">
                    <span>{{selectItem.districName}}</span>
                    <img src="@/assets/images/mall/user_addressList_icon_choice.png" alt="">
                </div>
                <div class="selectItem-l" v-if="selectDataIndex==3" v-for="(selectItem,i) in selectData"
                    :class="{'pitchUpItem-l':selectIndex == i}" @click="selectCity(i)">
                    <span>{{selectItem.areaName}}</span>
                    <img src="@/assets/images/mall/user_addressList_icon_choice.png" alt="">
                </div>
            </div>
        </div>
    </div>
</template>
<script>
    import { mapActions, mapState } from "vuex";
    import { Toast, Dialog } from "vant";
    import {storage} from '../../utils/storage'
    import NavigationBar from '@/common/currency/NavigationBar.vue';
    import {
        updateRecAddrService,
        addRecAddrService,
        delRecAddrService,
        queryProvinceService,
        getCityListService,
        queryDistrictService,
        getVirtualAreaCodesService,
        getAreaListService
    } from '../../services/shippingAddressService.js';
    export default {
        name: 'setAddress',
        components: {NavigationBar},
        data() {
            return {
                recName: '',
                isShowSelectBox: false,//是否显示选择城市盒子 false 隐藏  true 显示
                isShowbackToConfirm: false,//是否显示后退确认 false 隐藏  true 显示
                recName: "",  //收货人
                recPhone: "", //手机号码
                regionData: {}, //收货地区
                recAddressDetail: "",//详细地址
                selectDataIndex: 0, // 0 获取省 1 市 2 区 3 小区
                recAddrItem: {},
                flag: 0,//0 新增收货地址 1 编辑收货地址 2 订单详情入口进来的--新增收货地址 3 订单详情入口进来的--编辑收货地址
                selectData: [],//存储选择列表数据
                checked:false,
                themeCurrent: 0,
                virtualAreaCodes:[],//虚拟小区集合
                isVirtualArea: false
            }
        },
        mounted() {
            this.themeCurrent = this.$curTheme;
            this.flag = this.$route.params.data.flag;
            if (this.flag == 1 || this.flag == 3) {
                this.recAddrItem = this.$route.params.data.recAddrItem // 传来的收货地址数据
                this.setAddressData(); //设置页面数据
            }
            this.queryVirtualAreaCodes();//查询虚拟小区集合
            this.queryProvince(); //获取省份
        },
        activated() {
        },
        watch: {
      /*监控到物理返回键点击状态*/
      addressEventBackButtonFlag(val) {
        console.log("addressEventBackButtonFlag")
        if (val) {
          this.$store.dispatch('setAddressEventBackButtonFlag', false);
          if(this.isShowSelectBox){
            this.isShowSelectBox = false;
          }else
          {
            this.goBack()
          }
        }
      }
    },
        computed: mapState({
            addressEventBackButtonFlag: state => state.goBack.addressEventBackButtonFlag
        }),
        methods: {

            // 设置页面数据
            setAddressData: function () {
                this.recName = this.recAddrItem.recName;
                this.recPhone = Number(this.recAddrItem.recPhone);
                this.regionData.recProvince = this.recAddrItem.recProvince;
                this.regionData.recCity = this.recAddrItem.recCity;
                this.regionData.recDistrict = this.recAddrItem.recDistrict;
                this.regionData.recAreaName = this.recAddrItem.recAreaName;
                this.regionData.recAreaCode = this.recAddrItem.recAreaCode;
                this.recAddressDetail = this.recAddrItem.recAddressDetail;
                this.isDefault = this.recAddrItem.isDefault;
                if (this.recAddrItem.isDefault == 1) {
                    this.checked =true;
                }
            },
            //保存修改的数据
            saveData: function () {
                if (this.checked) {
                    this.isDefault = 1
                } else {
                    this.isDefault = 0
                }
                if (this.recName == '') {
                    this.$toast("请填写收货人名称");
                    return;
                }
                if (!(/^[A-Za-z0-9\u4e00-\u9fa5]+$/.test(this.recName))) {
                    this.$toast("收货人姓名只能包含数字、字母和汉字");
                    return;
                }
                if (!(/^1[3456789]\d{9}$/.test(this.recPhone))) {
                    this.$toast("手机号码有误，请重新填写");
                    return;
                }
                if (this.recPhone == '') {
                    this.$toast("请填写手机号码");
                    return;
                }
                if (this.regionData.recProvince == ''||this.regionData.recProvince == undefined||this.regionData.recProvince == null) {
                    this.$toast("请选择省");
                    return;
                }
                if (this.regionData.recCity == ''||this.regionData.recCity == undefined||this.regionData.recCity == null) {
                    this.$toast("请选择市");
                    return;
                }
                if (this.regionData.recDistrict == ''||this.regionData.recDistrict == undefined||this.regionData.recDistrict == null) {
                    this.$toast("请选择区");
                    return;
                }
                if ((this.regionData.recAreaName == ''||this.regionData.recAreaName == undefined||this.regionData.recAreaName == null)&&(!this.isVirtualArea)) {
                    this.$toast("请选择小区");
                    return;
                }
                if ((this.regionData.recAreaCode == ''||this.regionData.recAreaCode == undefined||this.regionData.recAreaCode == null)&&(!this.isVirtualArea)) {
                    this.$toast("请选择小区");
                    return;
                }
                if (this.recAddressDetail == '') {
                    this.$toast("请填写详细地址");
                    return;
                }

                if (this.flag == 1 || this.flag == 3) {
                    var data = {
                        recCode: this.recAddrItem.recCode,
                        ownerCode: this.recAddrItem.ownerCode,
                        recName: this.recName,
                        recPhone: this.recPhone,
                        recProvince: this.regionData.recProvince,
                        recCity: this.regionData.recCity,
                        recDistrict: this.regionData.recDistrict,
                        recAreaCode: this.regionData.recAreaCode,
                        recAreaName: this.regionData.recAreaName,
                        recAddressDetail: this.recAddressDetail,
                        isDefault: this.isDefault,
                        areaCode:this.$userInfo.AREA_CODE,
                        areaName:this.$userInfo.AREA_NAME
                    }
                    this.updateRecAddr(data);
                } else if (this.flag == 0 || this.flag == 2) {
                    var data = {
                        ownerCode: this.$userInfo.OWNER_CODE,
                        recName: this.recName,
                        recPhone: this.recPhone,
                        recProvince: this.regionData.recProvince,
                        recCity: this.regionData.recCity,
                        recDistrict: this.regionData.recDistrict,
                        recAreaCode: this.regionData.recAreaCode,
                        recAreaName: this.regionData.recAreaName,
                        recAddressDetail: this.recAddressDetail,
                        isDefault: this.isDefault,
                        areaCode:this.$userInfo.AREA_CODE,
                        areaName:this.$userInfo.AREA_NAME
                    }
                    this.addRecAddr(data);
                }
            },
            //修改收货地址
            updateRecAddr: function (data) {

                updateRecAddrService(data).then(res => {

                    if (res.resultCode == 0) {
                        this.$toast("修改成功");
                        if (this.flag == 3) {
                            storage.setStorage("recItem", res.data.recAddr, 999);
                        }
                        this.goToBack();
                    }else
                    {
                        Toast(res.msg);
                    }
                    console.log(res)
                }).catch(error => {
                })
            },
            //新增收货地址
            addRecAddr: function (data) {

                addRecAddrService(data).then(res => {

                    if (res.resultCode == 0) {
                        this.$toast("添加成功");
                        if (this.flag == 2) {
                            storage.setStorage("recItem", res.data.recAddr, 999);
                        }
                        this.goToBack();
                    }else
                    {
                        Toast(res.msg);
                    }
                    console.log(res)
                }).catch(error => {
                })
            },
            delRecAddrBtn: function () {
                Dialog.confirm({
                    title: '确定要删除地址吗？'
                }).then(() => {
                    // 确定
                    this.delRecAddr();
                }).catch(() => {
                    // on cancel
                });
            },
            //删除收货地址
            delRecAddr: function () {
                var data = {
                    recCode: this.recAddrItem.recCode,
                    ownerCode: this.recAddrItem.ownerCode
                }
                delRecAddrService(data).then(res => {
                    if (res.resultCode == 0) {
                        this.$toast("删除成功");
                        this.goToBack();
                    }else
                    {
                        Toast(res.msg);
                    }
                    console.log(res)
                }).catch(function (error) {

                })
            },
            //选择的数据
            selectCity: function (index) {
                this.selectIndex = index;
                //选择的城市
                if (this.selectDataIndex == 0) {
                    this.$refs.selectContent.scrollTop = 0;
                    this.regionData.recProvince = this.selectData[index].provinceName;
                    this.regionData.recCity = ""
                    this.regionData.recDistrict = ""
                    this.regionData.recAreaName = ""
                    this.getCityList(this.selectData[index].provinceName);
                    this.$refs.selectContent.scrollTop = 0;
                } else if (this.selectDataIndex == 1) {
                    this.regionData.recCity = this.selectData[index].cityName;
                    this.regionData.recDistrict = ""
                    this.regionData.recAreaName = ""
                    this.queryDistrict(this.selectData[index].cityName);
                    this.$refs.selectContent.scrollTop = 0;
                } else if (this.selectDataIndex == 2) {
                    this.regionData.recDistrict = this.selectData[index].districName;
                    this.regionData.recAreaName = ""
                    if(this.isVirtualArea){
                        //如果是虚拟小区
                        this.hideSelectBox();
                        return;
                    }
                    this.getAreaList(this.selectData[index].districName);
                    this.$refs.selectContent.scrollTop = 0;
                } else if (this.selectDataIndex == 3) {
                    this.regionData.recAreaName = this.selectData[index].areaName;
                    this.regionData.recAreaCode = this.selectData[index].areaCode;
                    this.hideSelectBox();
                    this.$refs.selectContent.scrollTop = 0;
                }
            },
            // 获取虚拟小区
            queryVirtualAreaCodes: function () {
                getVirtualAreaCodesService().then(res => {
                    if (res.resultCode == 0) {
                        this.virtualAreaCodes = res.data;
                        if (this.virtualAreaCodes.indexOf(this.$userInfo.AREA_CODE) !== -1) {
                            this.isVirtualArea = true;
                        }else{
                            this.isVirtualArea = false;
                        }
                    }
                    console.log(res)
                }).catch(error => {
                })
            },
            // 获取省份
            queryProvince: function () {
                var data = {
                    areaCode:this.$userInfo.AREA_CODE
                }
                queryProvinceService(data).then(res => {
                    if (res.resultCode == 0) {
                        this.selectData = res.data;
                        console.log(this.selectData)
                        this.selectDataIndex = 0; // 省标志
                        this.selectIndex = -1;
                    }
                    console.log(res)
                }).catch(error => {
                })
            },
            // 获取市
            getCityList: function (name) {
                var data = {
                    provinceName: name,
                    areaCode:this.$userInfo.AREA_CODE
                }
                getCityListService(data).then(res => {
                    if (res.resultCode == 0) {
                        this.selectData = res.data;
                        this.selectDataIndex = 1; // 市标志
                        this.selectIndex = -1;
                    }
                    console.log(res)
                }).catch(error => {
                })
            },
            // 获取区
            queryDistrict: function (name) {
                var data = {
                    cityName: name,
                    areaCode:this.$userInfo.AREA_CODE
                }
                queryDistrictService(data).then(res => {
                    if (res.resultCode == 0) {
                        this.selectData = res.data;
                        this.selectDataIndex = 2; // 区标志
                        this.selectIndex = -1;
                    }
                    console.log(res)
                }).catch(error => {
                })
            },
            // 获取小区
            getAreaList: function (name) {
                var data = {
                    districtName: name,
                    areaCode:this.$userInfo.AREA_CODE
                }
                getAreaListService(data).then(res => {

                    if (res.resultCode == 0) {
                        this.selectData = res.data;
                        this.selectDataIndex = 3; // 小区标志
                        this.selectIndex = -1;
                    }
                    console.log(res)
                }).catch(error => {
                })
            },
            // 显示后退确认盒子
            showBackToConfirm: function () {
                Dialog.confirm({
                    message: '收货地址还没有保存，<br>确定现在返回吗？'
                }).then(() => {
                    // 确定
                    this.$router.back();
                }).catch(() => {
                    // on cancel
                });
            },
            // 隐藏后退确认盒子
            hideBackToConfirm: function () {
                this.isShowbackToConfirm = false;
            },
            // 确定后退确认盒子
            trueBackToConfirm: function () {
                this.isShowbackToConfirm = false;
                this.goBack();
            },
            // 显示选择城市盒子
            showSelectBox: function () {
                this.isShowSelectBox = true;
            },
            // 隐藏选择城市盒子
            hideSelectBox: function () {
                this.isShowSelectBox = false;
            },
            goBack: function () {
                this.showBackToConfirm();
            },
            goToBack: function (){
                this.$router.back();
            }
        }
    }
</script>
<style>
    .setAddress {
        height: 100%;
    }
    .setAddress .headerColor {
        background: #FFFFFF;
        box-shadow: 0 0 1px 0 rgba(100, 100, 100, 0.50);
    }

    .setAddress .setAddressContent {
        background: #f9f9f9;
        height: 100%;
    }

    /* 收货信息 */
    .setAddress .ressInfo {
        padding: 17px 20px 1px;
        background: #fff;
    }
    .setAddress .ressInfo-l {
        padding: 17px 20px 1px;
        background: #fff;
    }

    .setAddress .consigneeInfo {
        display: flex;
        align-items: center;
        line-height: 56px;
        height: 56px;
        border-bottom: 0.5px solid #e5e5e5;
    }

    .setAddress .consigneeInfo:last-child {
        border: 0;
    }

    .setAddress .ressInfo label {
        font-family: PingFangSC-Regular;
        width: 90px;
        height: 22px;
        font-size: 16px;
        color: #9A9A9A;
        letter-spacing: -0.06px;
        line-height: 22px;
    }

    .setAddress .ressInfo-l label {
        font-family: PingFangSC-Regular;
        width: 90px;
        height: 22px;
        font-size: 17px;
        color: #9A9A9A;
        letter-spacing: -0.06px;
        line-height: 22px;
    }

    .setAddress .ressInfo input {
        font-family: PingFangSC-Medium;
        width: 66%;
        font-size: 16px;
        color: #333333;
        letter-spacing: -0.05px;
        /*line-height: 22px;*/
        padding: 0;
        height: 22px;
        border: 0;
    }
    .inputLineHeight{
        line-height: 22px;
    }
    .setAddress .ressInfo-l input {
        font-family: PingFangSC-Medium;
        width: 66%;
        font-size: 17px;
        color: #333333;
        letter-spacing: -0.05px;
        /*line-height: 22px;*/
        padding: 0;
        height: 22px;
        border: 0;
    }

    /* .consigneeInfo input{
    width: 75%;
} */
    .setAddress .setAddressContent .lineBox {
        height: 14px;
        background: #F9F9F9;
    }

    .setAddress .reginInfo {
        justify-content: space-between;
    }

    .setAddress .regionRight {
        width: 100%;
        display: flex;
        align-items: center;
    }

    .setAddress .reginInfo img {
        height: 14px;
    }

    .setAddress .reginLeftTxt {
        width: 66%;
        font-size: 16px;
        color: #333333;
        letter-spacing: -0.06px;
        line-height: 22px;
        overflow: hidden;
        text-overflow: ellipsis;
        white-space: nowrap;
    }

    .setAddress .regionTxt {
        width: 72%;
        font-size: 16px;
        color: #9A9A9A;
        letter-spacing: -0.06px;
        line-height: 22px;
        text-align: right;
    }

    /* 设置默认地址 */
    .setAddress .defaultBox {
        display: flex;
        justify-content: space-between;
        align-items: center;
        padding: 18px 20px;
        background: #fff;
    }

    .setAddress .defaultBtn {
        font-family: PingFangSC-Medium;
        font-size: 16px;
        color: #333333;
        letter-spacing: -0.05px;
        line-height: 22px;
    }

    .setAddress .defaultBtn-l {
        font-family: PingFangSC-Medium;
        font-size: 18px;
        color: #333333;
        letter-spacing: -0.05px;
        line-height: 22px;
    }

    .setAddress .ios-checkbox {
